import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }
  // ng-el
  openOgb() {
    this.resetContainer();
    const ogbElement = document.createElement('ng-el');
    const ogbElContainer = document.getElementById('product-container');
    ogbElContainer.appendChild(ogbElement);
  }

  // ng-el-key
  openKey() {
    this.resetContainer();
    const keyElement = document.createElement('ng-el-key');
    const keyElContainer = document.getElementById('product-container');
    keyElContainer.appendChild(keyElement);
  }

  resetContainer() {
    document.getElementById('product-container').innerHTML = '';
  }
}
